.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2fr 2fr 2fr 2fr;
    grid-auto-flow: row;
    width: 400px;
    height: 300px;
}

.mathParams{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    background-color: #5bff9e;
}

.value{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
    background-color: #97ffce;
}